html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  min-width: 320px;
  font-family: "Mulish", sans-serif;
  font-size: 18px;
  color: #7d7987;
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1277px;
}

.site-container {
  overflow: hidden;
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.is-hidden {
  display: none !important;
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  padding-top: 59px;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;
}

.logo__svg {
  width: 41px;
  height: 41px;
  fill: #458ff6;
}

.logo__txt {
  margin-left: 12px;
  font-weight: 700;
  font-size: 24px;
  color: #233348;
}

.nav {
  width: 545px;
}

.nav__list {
  display: flex;
  justify-content: space-between;
}

.nav__link {
  color: #1f1534;
  opacity: 0.5;
  transition: 0.2s;
}

.nav__link:hover {
  font-weight: 700;
  opacity: 1;
}

.burger {
  position: relative;
  z-index: 10;
  display: none;
  flex-direction: column;
  align-items: center;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-color: #458ff6;
  cursor: pointer;
}

.burger__line {
  position: absolute;
  display: block;
  border-radius: 4px;
  padding: 0;
  width: 18px;
  height: 2px;
  background-color: #fff;
  transition: all 0.3s ease-in-out, transform 0.3s ease-in-out 0.3s;
}

.burger__line:nth-child(1) {
  top: 14px;
}

.burger__line:nth-child(2) {
  transform: rotate(90deg);
}

.burger__line:nth-child(2),
.burger__line:nth-child(3) {
  left: 19px;
  top: 19px;
  width: 1px;
}

.burger__line:nth-child(3) {
  right: 0;
}

.burger__line:nth-child(4) {
  top: 19px;
  bottom: 0;
}

.burger__line:nth-child(5) {
  bottom: 14px;
}

.burger_active .burger__line:nth-child(1) {
  top: 19px;
  background-color: transparent;
}

.burger_active .burger__line:nth-child(2) {
  left: 10.5px;
  width: 18px;
  transform: rotate(45deg);
}

.burger_active .burger__line:nth-child(3) {
  left: 10.5px;
  width: 18px;
  transform: rotate(-45deg);
}

.burger_active .burger__line:nth-child(4) {
  background-color: transparent;
}

.burger_active .burger__line:nth-child(5) {
  bottom: 19px;
  background-color: transparent;
}

.hero__container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 53px;
}

.hero__dots-decore {
  position: absolute;
  left: -160px;
  top: 60px;
  z-index: 10;
  width: 79px;
  height: 113px;
  background: url("../img/dots-decor.png") center/cover no-repeat;
}

.hero__desc {
  width: 445px;
}

.hero__title {
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
  color: #000;
}

.hero__txt {
  margin-top: 25px;
  font-weight: 300;
  font-size: 21px;
  line-height: 32px;
}

.hero__btn {
  margin-top: 45px;
}

.hero__img {
  width: 693px;
  height: 598px;
  background: url("../img/hero__img.jpg") center/cover no-repeat;
}

.btn {
  display: block;
  border: 1.4px solid #458ff6;
  border-radius: 55px;
  width: 200px;
  height: 60px;
  font-weight: 700;
  line-height: 57px;
  text-align: center;
  color: #458ff6;
  background-color: transparent;
  transition: 0.4s;
  cursor: pointer;
}

.btn:hover {
  color: #fff;
  background-color: #458ff6;
}

.btn_revers {
  color: #fff;
  background-color: #458ff6;
}

.btn_revers:hover {
  color: #458ff6;
  background-color: transparent;
}

.services {
  padding-top: 216px;
}

.services__container {
  position: relative;
}

.services__section-txt {
  text-align: center;
}

.services__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 80px;
  padding: 0 48px;
  gap: 36px 36px;
}

.services__btn {
  margin: 69px auto 0;
}

.blot-decore {
  position: absolute;
  left: -405px;
  top: 0;
  z-index: -1;
  width: 1337px;
  height: 971px;
}

.section-txt__title {
  position: relative;
  font-weight: 700;
  font-size: 36px;
  line-height: 56px;
  color: #000;
}

.section-txt__title::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 107px;
  bottom: 0;
  margin: auto;
  border-radius: 5px;
  width: 56px;
  height: 2px;
  background-color: #000;
}

.section-txt__parag {
  margin: 61px auto 0;
  max-width: 952px;
  line-height: 30px;
}

.card {
  justify-self: center;
  border-radius: 20px;
  padding: 68px 40px 39px;
  box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
  background-color: #fff;
}

.card__svg {
  width: 90px;
  height: 90px;
}

.card__title {
  margin-top: 20px;
  font-weight: 700;
  font-size: 24px;
  color: #000;
}

.card__txt {
  margin-top: 20px;
  font-weight: 300;
  font-size: 16px;
  line-height: 28px;
}

.download {
  padding-top: 220px;
}

.download__container {
  display: flex;
  flex-direction: column;
}

.download__item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.download__item:last-child {
  margin-top: 219px;
}

.download__img {
  width: 650px;
  height: 477px;
}

.download__img_first {
  margin-left: -55px;
  background: url("../img/download__img_1.jpg") center/cover no-repeat;
}

.download__img_second {
  order: 1;
  background: url("../img/download__img_2.jpg") center/cover no-repeat;
}

.download__txt {
  max-width: 365px;
}

.download__txt_first {
  margin: 62px 116px 0 0;
}

.download__txt_second {
  margin: 0 0 0 90px;
}

.download__title {
  position: relative;
  font-weight: 700;
  font-size: 36px;
  line-height: 56px;
  color: #000;
}

.download__title::after {
  content: "";
  position: absolute;
  left: 0;
  top: 140px;
  border-radius: 10px;
  width: 56px;
  height: 2px;
  background-color: #000;
}

.download__parag {
  margin-top: 59px;
  font-weight: 300;
  font-size: 18px;
  line-height: 30px;
}

.download__btn {
  margin-top: 37px;
}

.download__btn:hover .download__arrow_down {
  fill: #fff;
}

.download__arrow_down {
  margin-left: 15px;
  width: 15px;
  height: 15px;
  fill: #458ff6;
  transition: 0.4s;
}

.articles {
  padding-top: 200px;
}

.articles__section-txt {
  text-align: center;
}

.articles__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-top: 90px;
}

.articles__img {
  border-radius: 20px 20px 0 0;
  width: 350px;
  height: 240px;
}

.articles__img_1 {
  background: url("../img/articles__img_1.jpg") center/cover no-repeat;
}

.articles__img_2 {
  background: url("../img/articles__img_2.jpg") center/cover no-repeat;
}

.articles__img_3 {
  background: url("../img/articles__img_3.jpg") center/cover no-repeat;
}

.articles__txt {
  border-radius: 0 0 20px 20px;
  padding: 20px 50px 30px 30px;
  width: 350px;
  box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
  background-color: #fff;
}

.articles__title {
  font-weight: 700;
  font-size: 21px;
  line-height: 32px;
  color: #000;
}

.articles__parag {
  margin-top: 12px;
  font-weight: 300;
  font-size: 16px;
  line-height: 28px;
}

.articles__link {
  display: inline-block;
  margin-top: 25px;
  border-radius: 0 10px 10px 0;
  padding-right: 10px;
  font-size: 17px;
  line-height: 28px;
  color: #458ff6;
  transition: 0.4s;
  cursor: pointer;
}

.articles__link:hover {
  color: #fff;
  background-color: #458ff6;
}

.articles__link:hover .articles__svg {
  fill: #fff;
}

.articles__svg {
  margin-left: 13px;
  width: 14px;
  height: 9px;
  fill: #458ff6;
  transition: 0.4s;
}

.articles__btn {
  margin: 69px auto 0;
}

@media (max-width: 1200px) {
  .hero__container {
    padding-top: 100px;
  }

  .hero__img {
    width: 460px;
    height: 400px;
  }

  .services__list {
    grid-template-columns: repeat(2, 1fr);
  }

  .download {
    padding-top: 150px;
  }

  .download__item {
    justify-content: space-evenly;
  }

  .download__item:last-child {
    margin-top: 100px;
  }

  .download__img {
    width: 450px;
    height: 330px;
  }

  .download__txt_first {
    margin: 0;
  }

  .download__txt_second {
    margin: 0;
  }

  .articles__img {
    width: 250px;
    height: 140px;
  }

  .articles__txt {
    padding: 15px;
    width: 250px;
    text-align: center;
  }

  .articles__title {
    font-size: 18px;
    line-height: 25px;
  }

  .articles__parag {
    font-size: 14px;
    line-height: 23px;
  }

  .articles__link {
    font-size: 15px;
  }
}

@media (max-width: 970px) {
  .nav {
    display: none;
  }

  .burger {
    display: flex;
  }

  .hero__container {
    justify-content: space-evenly;
  }

  .hero__desc {
    max-width: 288px;
  }

  .hero__title {
    font-size: 30px;
    line-height: 39px;
  }

  .hero__txt {
    margin-top: 8px;
    font-size: 18px;
    line-height: 27px;
  }

  .hero__btn {
    margin-top: 27px;
  }

  .hero__img {
    width: 360px;
    height: 300px;
  }

  .btn {
    width: 140px;
    height: 40px;
    font-size: 15px;
    line-height: 38px;
  }

  .services {
    padding-top: 150px;
  }

  .section-txt__title {
    font-size: 22px;
  }

  .section-txt__title::after {
    top: 80px;
  }

  .section-txt__parag {
    margin: 30px auto 0;
    font-size: 16px;
  }

  .card {
    justify-self: auto;
    padding-top: 0;
  }

  .card__svg {
    width: 40px;
  }

  .card__title {
    margin-top: 0;
    font-size: 20px;
  }

  .card__txt {
    margin-top: 10px;
  }

  .download__img {
    width: 250px;
    height: 185px;
  }

  .download__title {
    font-size: 25px;
    line-height: 35px;
  }

  .download__title::after {
    top: 50px;
  }

  .download__parag {
    margin-top: 30px;
    font-size: 16px;
    line-height: 26px;
  }

  .articles__list {
    margin-top: 48px;
  }

  .articles__btn {
    margin-top: 25px;
  }
}

@media (max-width: 809px) {
  .articles__item:nth-child(3) {
    margin-top: 30px;
  }
}

@media (max-width: 700px) {
  .container {
    padding: 0 15px;
  }

  .header {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .hero__container {
    padding-top: 80px;
  }

  .hero__desc {
    z-index: 1;
    max-width: 100%;
  }

  .hero__img {
    position: absolute;
    right: 0;
    top: 135px;
    width: 260px;
    height: 220px;
    min-width: 260px;
    min-height: 220px;
  }

  .services__list {
    grid-template-columns: repeat(1, 1fr);
    padding: 0;
  }

  .download {
    padding-top: 69px;
  }

  .download__item {
    justify-content: flex-start;
  }

  .download__img {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: -1;
    width: 150px;
    height: 110px;
  }
}

@media (max-width: 650px) {
  body {
    font-size: 14px;
  }
}

@media (max-width: 529px) {
  .articles__item:nth-child(2) {
    margin-top: 30px;
  }
}

@media (max-width: 350px) {
  .download__title::after {
    top: 85px;
  }
}